.about-us{
    width: 100%;
    background-color: white;
    padding-bottom: 20px;
    .img-left{
        position: relative;
        width: 100%;
        img{
            width: 90%;
            padding: 5%;
        }
        .position-center{
            background-color: $teal;
            position: absolute;
            width: 85%;
            right: 0;
            bottom: 30px;
            color: white;
            *{
                padding-left: 15px;
            }
            h1{
                font-size: 50px;
                margin: 0;
            }
            p{
                margin: 10px;
            }
        }
    }
    .content-about{
        padding: 5%;
        color: gray;
        h1{
            font-size: 3rem;
        }
        .btn--black{
            border: black 2px solid;
            width: 150px !important;
            margin-top: 10px;
        }
    }
}

@media only screen and (min-width:767.98px){
    .about-us{
        display: grid;
        grid-template-columns: 60% auto;
        grid-column-gap: 5%;
        .content-about{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: left;
            width: 80%;
        }
    }
}